const modals = (triggerSelector, modalSelector, closeSelector, destroy = false) => {
    try{

        const trigger = document.querySelectorAll(triggerSelector),
        modal = document.querySelector(modalSelector),
        closes = document.querySelectorAll(closeSelector),
        modals = document.querySelectorAll(`[data-modal]`),
        scroll = calcScroll(),
        hashModal = modal.getAttribute(`data-modal`);
        

        

        trigger.forEach((item,i) => {
            item.addEventListener(`click`, (e) =>{
                e.preventDefault();

                window.history.pushState(`'${hashModal}'`, "Modal title", ``);
                
                if(destroy){
                    item.remove();
                }
                modals.forEach(item =>{
                    item.style.display = `none`;
                    item.classList.add(`fade-in`);
                });
                
                modal.classList.remove(`fade-out`);
                modal.classList.add(`fade-in`);
                modal.style.display = 'block';
                document.body.style.overflow = 'hidden';
                // document.body.style.marginRight = `${scroll}px`;
                
            });
    });
    window.addEventListener("popstate", function(e) {
        e.stopPropagation()
        e.preventDefault()
        
        history.go(-1); 
        modal.classList.add(`fade-out`);
        modal.style.display = 'none';
        modal.classList.remove(`fade-in`);
        document.body.style.overflow = 'auto';
        document.body.style.marginRight = `0px`;
        
    })
        
    closes.forEach(close =>{
        close.addEventListener(`click`, (e) =>{
            modal.classList.add(`fade-out`);
            modal.style.display = 'none';
            modal.classList.remove(`fade-in`);
            document.body.style.overflow = 'auto';
            document.body.style.marginRight = `0px`;
            modals.forEach(item =>{
                item.style.display = `none`;
            });
            // history.go(-1);          
        });   
    });

    modal.addEventListener(`click`, (e) =>{
        if(e.target === modal){
            modal.style.display = 'none';
            document.body.style.overflow = 'auto';
            document.body.style.marginRight = `0px`;
        }


    });

    function showModalBindTime(selector, time){
        let display;

        document.querySelectorAll(`[data-modal]`).forEach(item =>{
            if(getComputedStyle(item).display !== `none`){

                display = "block"
            }
            if(!display){
                setTimeout(()=>{
                    scroll = calcScroll();
                    document.body.style.marginRight = `${scroll}px`;
                    document.querySelector(selector).style.display = `block`;
                    document.body.style.overflow = 'hidden';
                },time);
            }
        });
    };


    function calcScroll(){
        let div = document.createElement(`div`);

        div.style.width = `50px`;
        div.style.height = `50px`;
        div.style.overflowY = `scroll`;
        div.style.visibility = `hidden`;
        
        document.body.appendChild(div);
        let scrollWidth = div.offsetWidth - div.clientWidth;

        div.remove()

        return scrollWidth;
    }

    function openByScroll(selector){
        window.addEventListener(`scroll`, () =>{
            let scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);
            if(window.pageYOffset + document.documentElement.clientHeight >= scrollHeight){
                document.querySelector(selector).click();
            }
        });
    }


    // showModalBindTime(`.modal-card` , 60000);
    
    
    }catch(e){}
    
};

export default modals;